/* eslint-disable @next/next/no-img-element */
'use client';

import React, { useState } from 'react';
import PaginationComponent from '@/components/pagination/page';



export default function AddNewProduct() {
  const [currentPage, setCurrentPage] = useState(1);
  const [searchTerm, setSearchTerm] = useState('');
  const itemsPerPage = 5;
  
  // Pagination and filtering logic
  const filteredProducts = products.filter((product) =>
    product.name.toLowerCase().includes(searchTerm.toLowerCase())
  );
  const totalPages = Math.ceil(filteredProducts.length / itemsPerPage);
  const displayedProducts = filteredProducts.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  const handlePageChange = (event: React.ChangeEvent<unknown>, page: number) => {
    setCurrentPage(page);
  };

  return (
    <section className="p-6 bg-white rounded-lg shadow-lg">
      <h2 className="text-2xl font-semibold text-gray-800 mb-6">Add New Product</h2>

      {/* Add New Product Form */}
      <div className="mb-6 bg-gray-100 p-6 rounded-lg shadow-md">
        <h3 className="text-xl font-semibold text-gray-700 mb-4">Add Product Details</h3>
        <form className="space-y-4">
          <div className="flex space-x-4">
            <input
              type="text"
              placeholder="Product Name"
              className="w-1/2 p-3 border border-gray-300 rounded-lg"
            />
            <input
              type="text"
              placeholder="Product Price"
              className="w-1/2 p-3 border border-gray-300 rounded-lg"
            />
          </div>
          <div className="flex space-x-4">
            <input
              type="text"
              placeholder="Product Category"
              className="w-1/2 p-3 border border-gray-300 rounded-lg"
            />
            <input
              type="file"
              className="w-1/2 p-3 border border-gray-300 rounded-lg"
            />
          </div>
          <textarea
            placeholder="Product Description"
            rows={4}
            className="w-full p-3 border border-gray-300 rounded-lg"
          />
          <button
            type="submit"
            className="w-full py-3 bg-blue-500 text-white font-semibold rounded-lg shadow hover:bg-blue-600 transition"
          >
            Add Product
          </button>
        </form>
      </div>

      {/* Existing Products Table */}
      <div className="mb-6">
        <div className="flex justify-between items-center mb-4">
          <input
            type="text"
            placeholder="Search products..."
            className="px-4 py-2 border border-gray-300 rounded-lg w-1/3"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
          <select className="px-4 py-2 border border-gray-300 rounded-lg">
            <option>Sort by Name</option>
            <option>Sort by Price</option>
            <option>Sort by Date</option>
          </select>
        </div>

        <div className="overflow-x-auto bg-white rounded-lg shadow">
          <table className="min-w-full text-sm text-gray-600">
            <thead className="bg-gray-100">
              <tr>
                <th className="px-6 py-3 font-medium text-left">Image</th>
                <th className="px-6 py-3 font-medium text-left">Product ID</th>
                <th className="px-6 py-3 font-medium text-left">Name</th>
                <th className="px-6 py-3 font-medium text-left">Price</th>
                <th className="px-6 py-3 font-medium text-left">Actions</th>
              </tr>
            </thead>
            <tbody>
              {displayedProducts.map((product) => (
                <tr key={product.id} className="border-t hover:bg-gray-50">
                  <td className="px-6 py-4">
                    <img
                      src={product.image}
                      alt={product.name}
                      className="w-16 h-16 rounded-lg object-cover"
                    />
                  </td>
                  <td className="px-6 py-4 text-gray-800">#{product.id}</td>
                  <td className="px-6 py-4 text-gray-800">{product.name}</td>
                  <td className="px-6 py-4 font-semibold text-gray-900">{product.price}</td>
                  <td className="px-6 py-4">
                    <button className="px-4 py-2 text-sm text-white bg-blue-500 rounded-lg shadow hover:bg-blue-600 transition">
                      View Details
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        <PaginationComponent
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={handlePageChange}
        />
      </div>
    </section>
  );
}














const products = [
  { id: 1, name: 'Wireless Mouse', price: '$25.00', image: 'https://via.placeholder.com/150' },
  { id: 2, name: 'Office Chair', price: '$120.00', image: 'https://via.placeholder.com/150' },
  { id: 3, name: 'Gaming Keyboard', price: '$75.00', image: 'https://via.placeholder.com/150' },
  { id: 4, name: 'Desk Lamp', price: '$45.00', image: 'https://via.placeholder.com/150' },
  { id: 5, name: 'Bookshelf', price: '$200.00', image: 'https://via.placeholder.com/150' },
  { id: 6, name: 'Bluetooth Speaker', price: '$50.00', image: 'https://via.placeholder.com/150' },
  { id: 7, name: 'Smartphone Holder', price: '$15.00', image: 'https://via.placeholder.com/150' },
  { id: 8, name: 'Monitor Stand', price: '$35.00', image: 'https://via.placeholder.com/150' },
  { id: 9, name: 'Wireless Charger', price: '$30.00', image: 'https://via.placeholder.com/150' },
  { id: 10, name: 'LED Light Strips', price: '$20.00', image: 'https://via.placeholder.com/150' },
];